.mui-header{box-sizing:border-box;padding-left:15px;padding-right:15px;height:44px;background-color:#fff;position:relative}.mui-header:after{content:" ";position:absolute;left:0;bottom:0;width:100%;height:1px;border-bottom:1px solid #D9D9D9;color:#D9D9D9;-webkit-transform-origin:0 100%;transform-origin:0 100%;-webkit-transform:scaleY(0.5);transform:scaleY(0.5)}.mui-header-left{text-align:left}.mui-header-middle{text-align:center}.mui-header-right{text-align:right}.mui-header-back{color:#0092D8;line-height:44px}.mui-header-back:before{content:" ";display:inline-block;border-color:#0092D8;border-style:solid;height:14px;width:14px;border-width:2px 2px 0 0;-webkit-transform:rotate(-135deg);transform:rotate(-135deg);position:relative;top:-2px;top:2px;margin-left:.3em}h1.mui-header-title{line-height:44px;font-weight:normal;color:#333;font-size:16px;margin:0}



