.mui-popside-dialog{position:absolute;left:0;bottom:0;width:100%;overflow-y:scroll;-webkit-overflow-scrolling:touch;background:#fbf9fe;z-index:101;box-shadow:-3px 0px 8px 0px rgba(50,50,50,0.75);box-sizing:border-box}.mui-popside-transition{transition-property:-webkit-transform;-webkit-transition-property:-webkit-transform;transition-property:transform;transition-property:transform, -webkit-transform;-webkit-transition-duration:300ms;transition-duration:300ms}.mui-popside-enter{background-color:red;-webkit-transform:translate3d(100%, 0, 0);transform:translate3d(100%, 0, 0)}.mui-popside-leave{-webkit-transform:translate3d(100%, 0, 0);transform:translate3d(100%, 0, 0)}



